import React from 'react'
import { AnimatePresence, motion } from "framer-motion";
import "../App.css";

function Projects() {
    const pageTransition1 = {
        in:{opacity:1},
        out:{opacity:0}
      }
    return (
        <motion.div className="Projects" exit="out" animate="in" initial="out" variants={pageTransition1}>
            <h1>Projects here Soon !</h1>
        </motion.div>
    )
}

export default Projects
