import React from 'react'
import { AnimatePresence, motion } from "framer-motion";
import "../App.css";

function Skills() {
    const pageTransition3 = {
        in:{opacity:1},
        out:{opacity:0}
      }
    return (
        <motion.div className="Skills" exit="out" animate="in" initial="out" variants={pageTransition3}>
            <h1>Skills here Soon ! </h1>
        </motion.div>
    )
}

export default Skills
