import React from "react";
import { AnimatePresence, motion } from "framer-motion";
import "../App.css";
import MyMoji from "../images/MyMoji1.png";

function About() {
  const pageTransition2 = {
    in: { opacity: 1 },
    out: { opacity: 0 },
  };
  return (
    <motion.div
      className="About"
      exit="out"
      animate="in"
      initial="out"
      variants={pageTransition2}
    >
      <div className="AboutCardContainer">
        <img rel="preload" src={MyMoji} alt="Image" draggable="false" />
      </div>
      <div className="AboutDescription">
          <p><br></br>Hello there ! I am Dev from India.</p>
      </div>
    </motion.div>
  );
}

export default About;
